import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

class Footer extends Component {
  render() {
    return (
      <Grid container alignContent="center" alignItems="center" justify="center" className="silver bg-dark-gray pv4">
        <Grid item>
          <Typography color="inherit">© 2018 Codemitter Technology Pvt. Ltd.</Typography>
        </Grid>
      </Grid>
      // <footer className="silver bg-dark-gray pa3">
      //   <div className="mh7 mv3 tc">
      //     © 2018 Codemitter Technology Pvt. Ltd.
      //           </div>
      // </footer>
    );
  }
}

export default Footer;



