import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
// import withMobileDialog from '@material-ui/core/withMobileDialog';

class ResponsiveDialog extends React.Component {
    state = {
        open: false,
    };
    handleClose = () => {
        this.setState({ open: false });
    };

    render() {
        const { fullScreen, open, title, content, negativeHandler, positiveHandler, cancelHandler } = this.props;

        return (
            <div>
                <Dialog
                    fullScreen={fullScreen}
                    open={open}
                    onClose={e=> {this.handleClose(e);cancelHandler(e);}}
                    aria-labelledby={title}
                >
                    <DialogTitle id={title}>{title}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            {content}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={e=> {this.handleClose(e);negativeHandler(e);}} color="primary" autoFocus>
                            No
                        </Button>
                        <Button onClick={e=> {this.handleClose(e);positiveHandler(e);}} color="primary">
                            Yes
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

ResponsiveDialog.propTypes = {
    fullScreen: PropTypes.bool.isRequired,
};

export default ResponsiveDialog;


