import { gql } from 'apollo-boost';

export const USERS = gql`
    query users($limit: Int = 15){
        users(limit: $limit) {
            id,
            username,            
            totalReceivedAmount,
            spent,
            balance,
            createdAt,
            updatedAt,
            count
        }
    }
`;

export const SITELIST = gql`
  query sites($limit: Int = 10) {
    sites(limit: $limit) {
        id,
        name,
        location,
        createdAt,
        updatedAt,
        count
        manager {
            name: username
        }
    }
  }
`;

export const USERS_SITELIST = gql`
    query users_sites($limit: Int = 15){
        users(limit: $limit) {
            id,
            username,
            lastCreditAmount,
            totalReceivedAmount,
            spentAmount,
            remainingAmount,
            createdAt,
            updatedAt,
            count
        }
        sites(limit: $limit) {
            id,
            name,
            location,
            createdAt,
            updatedAt,
            count,
            manager {
                username
            }
        }
    }
`;

const repeater = `{
    quantity,
    cost,
    paid
}`;

export const SITE = gql`
    query site($id: String!, $limit: Int!, $skip: Int){
        site(id: $id, limit: $limit, skip: $skip){
            id,
            name,
            location,
            cost,
            count,
            manager {
                name: username,                
            },
            managerSpentAmount,
            entries
            {
                id,
                createdAt,
                updatedAt,                
                mistri ${repeater},
                labour ${repeater},
                eit ${repeater},
                morang ${repeater},
                githi ${repeater},
                cement ${repeater},
                saria ${repeater},
                dust ${repeater},
                other ${repeater},
                total
            }
        }
    }
`



