import { gql } from 'apollo-boost';

export const LOGIN = gql`
    mutation login($data: LoginInput!){
        login(data: $data){
            token
        }
    }
`;

export const CREATESITE = gql`
mutation createSite($data: SiteInput) {
    createSite(data: $data) {
        id,
        name,
        location,
        manager {
            username
        }
    }
}
`;

export const MAKESITEENTRY = gql`
mutation makeSiteEntry(
    $siteId: String!,
    $data: SiteEntryInput!
) {
    makeSiteEntry(
        siteId: $siteId,
        data: $data
    ) {
        id        
    }
}
`;

export const CREATEUSER = gql`
mutation createUser($data: UserInput!){
    createUser(data: $data){
        id
    }
}
`

export const DELETESITEENTRY = gql`
mutation deleteSiteEntry($siteId: String!, $ids: [String!]!){
    deleteSiteEntry(siteId: $siteId, ids: $ids){
        status
    }
}
`

export const DELETESITES = gql`
mutation deleteSites($ids: [String!]!){
    deleteSites(ids: $ids){
        status
    }
}
`



